import './App.css';
import Table from "./component/Table"
import {connect} from "react-redux"
import {startDateData} from "./component/action/dateData"
import React, { useState } from "react";
import {startLogin} from "./component/action/loggedin"
import Login from "./component/Login"
import { Route} from "react-router-dom";
import {startProjName} from "./component/action/projName"




function App(props) {
  const [active, setActive] = useState(false);
  const [active1, setActive1] = useState(false);
  // const [loggedin, setActive] = useState(false);
  let loggedin = false
  const handleClick = () => {
    const drop_down_click = document.getElementById("drop_down_data");

    if (drop_down_click.style.display === "none") {
      drop_down_click.style.display = "block";
    } else {
      drop_down_click.style.display = "none";
    }
  };

  const handleClick1 = (data) => {
    setActive(true)
    props.dispatch(startDateData(data))
    const drop_down_click = document.getElementById("drop_down_data");
    if (drop_down_click.style.display === "block") {
      drop_down_click.style.display = "none";
    }
  }

  const handleClick2 = () => {
    const drop_down_click = document.getElementById("drop_down_project");

    if (drop_down_click.style.display === "none") {
      drop_down_click.style.display = "block";
    } else {
      drop_down_click.style.display = "none";
    }
  }

  const handleClick3 = (data) => {
    setActive1(true)
    props.dispatch(startProjName(data))
    const drop_down_click = document.getElementById("drop_down_project");
    if (drop_down_click.style.display === "block") {
      drop_down_click.style.display = "none";
    }
  }

  const logoutButton = () => {
    localStorage.removeItem("asm_token");
              localStorage.removeItem("asm_token_email");
              props.dispatch(startLogin(false));
  }


  if(localStorage.getItem("asm_token")){
    loggedin = true
  }
  else {
    loggedin = false
  }

// console.log(props.projName,"props-proj-data")
// console.log(props.totalDatat,"props-total-data")
// console.log(props.dateData,"props-date-data")

  return (
    <div className="App">
       {loggedin ? (
         <>

      <div className="header">
        <img style={{height:"48px"}} src={window.location.origin+"/images/ottLogo.png"} alt="ott" className="ottLogo"/>
        <h3 className="project_name">Hot Leads</h3>
        <div className="drop1" onClick={()=>logoutButton()} style={{width: "100px", justifyContent: "center"}}>Logout</div>
      </div>
      <div className="hot_row1">
        <div className="drop1">
          <p className="drop_data_p1" onClick={()=>handleClick2()}>{active1 ? props.projName: "Project"}</p>
          <div className="drop_down_project" id="drop_down_project" style={{display: "none" , width: "140px"}}>
            <p className="drop_p1" onClick={(id)=>handleClick3("ninjacart")}>ninjacart</p>  
        </div>
        </div>
        <div className="drop1">
        <p className="drop_data_p1" onClick={()=>handleClick()}>{active ? props.dateData.dates: "Date"}</p>
        <div className="drop_down_data" id="drop_down_data" style={{display: "none"}}>
          {props.totalData.map((date,indx)=>(
            <p className="drop_p1" key={indx} onClick={(id)=>handleClick1(date)}>{date.dates}</p>
          ))}
        </div>
        </div>
      </div>
      <div className="hot_row1" >
        <div className="hot_card1" style={{background: "#7a52ff",border: "1px solid #ac2828"}}>
          <p className="hot_p1">Total visited stores</p>
          <p className="hot_p2">{active ? props.dateData.total_checkins : ""}</p>
        </div>
        <div className="hot_card1" style={{background: "#4fdb6f",border: "1px solid #00802a"}}>
        <p className="hot_p1">Total orders</p>
          <p className="hot_p2">{active ? props.dateData.orders_placed.length : ""}</p></div>
        <div className="hot_card1" style={{background: "#fa5b4d",border: "1px solid #ac2828"}}>
        <p className="hot_p1" >Total denied</p>
          <p className="hot_p2">{active ? props.dateData.order_denied.length : ""}</p>
        </div>
      </div>
      <Route to="/dashboard">
          <Table/>
          </Route>
      </>)
      : (
<Route to="/login">
          <Login />
          </Route>

      )}
    </div>
  );
}


const mapStateToProps=(state)=>{
  return {
    totalData:state.totalData,
    dateData:state.dateData,
    loggedin:state.loggedin,
    projName:state.projName
  }
}

export default connect(mapStateToProps)(App)
