import React, { useState } from "react";
// import { useHistory } from "react-router-dom";
import { MyGoogleLogin } from "./GoogleLogin";
import {startLogin } from "./action/loggedin"
import "./Login.css";
import {connect} from "react-redux"


function Login(props) {
  // const history = useHistory();  
  const [loginError, setLoginError] = useState("");
  const loginFunc = (r) => {
    // console.log(r)
    localStorage.setItem("asm_token", r.accessToken);
    localStorage.setItem("asm_token_email", r.profileObj.email);
    props.dispatch(startLogin(true))

  };
  const logoutFunc = () => {
    props.dispatch(startLogin(false))

    localStorage.removeItem("asm_token");
    localStorage.removeItem("asm_token_email");
    
  };
  return (
    <div className="p_login">
      <div className="log_col1">
        <img
          src={window.location.origin + "/images/hot_lead_login.png"}
          alt="login"
          className="login_bg"
        />
      </div>
      <div className="log_col2">
        <h1 className="log_name">Taskmo HOT-Leads</h1>
        <div
          className="login_button"
        >
          <MyGoogleLogin loginFunc={loginFunc} logoutFunc={logoutFunc} setLoginError={setLoginError} />
        </div>
        <div style={{ marginTop: "10px" }}>{loginError}</div>
      </div>
    </div>
  );
}
const mapStateToProps=(state)=>{
  return {
    loggedin:state.loggedin
  }
}

export default connect(mapStateToProps)(Login);
