import {startGetTotalData} from "./totalData"

const getProjName=(data)=>{
  return {type:"GET_PROJECT_NAME",payload:data}
}

const startProjName=(data)=>{
  return (dispatch)=>{
          const response=data
          if(response){
          dispatch(getProjName(response))
          dispatch(startGetTotalData(response))
          }else {
          dispatch(getProjName(""))

          }
  }
}

export {
  startProjName,
  getProjName,
};