
const getDateData = (tl) => {
  return { type: "GET_DATE_DATA", payload: tl };
};

const startDateData = (data) => {
  return (dispatch) => {
    // console.log(id,"fse_id_redux")

    const loadData = async () => {
      try {
        // const DateData=await axios.post("/lead_stats")
        // console.log(data,"date_data_redux")
        dispatch(getDateData(data))
        
      } catch (err) {
        console.error(err);
      }
    };
    loadData();
  };
};



export { getDateData, startDateData  };
